import { Injectable } from '@angular/core';

import { ProductSummaryHttpService, ProductSummaryItem } from '@backbase/data-ang/arrangements';
import { Account } from '../model/account.model';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { fromArrangementsResponse } from '../model/account-list.model';

@Injectable()
export class AccountsService {
  constructor(protected readonly productSummaryDataHttpService: ProductSummaryHttpService) {}

  getAllArrangements(): Observable<Account[]> {
    return this.productSummaryDataHttpService
      .getArrangementsByBusinessFunction(
        {
          businessFunction: 'Product Summary',
          resourceName: 'Product Summary',
          privilege: 'view',
          size: 1000000,
        },
        'response',
      )
      .pipe(map(fromArrangementsResponse));
  }

  getProductSummary(): Observable<ProductSummaryItem[]> {
    return this.productSummaryDataHttpService.getArrangementsByBusinessFunction(
      {
        businessFunction: 'Product Summary',
        resourceName: 'Product Summary',
        privilege: 'view',
        size: 1000000,
      },
      'body',
    );
  }
}
